@import './theme/app/index.scss';

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


a {
  &:hover, &:link, &:hover, &:visited {text-decoration: none}
}

.align-center {
  text-align: center;
}

.align-left {
  text-align: left
}

.align-right {
  text-align: right
}


.line-dark {
  height: 1px;
  background: #000;
}

.line-dark-m {
  height: 1px;
  background: $dark-background-s;
}

.line-light {
  height: 1px;
  background: #fff;
}

.line-light-m {
  height: 1px;
  background: $light-background-s;
}

// flex
.row {
  display: flex;
  align-items: center;
}

.column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.center {
  justify-content: center;
  align-items: center;
}

.row-center {
  @extend .row;
  @extend .center;
}

.column-center {
  @extend .column;
  @extend .center;
}

/* --- Summary --- */

// 
.summary-wrapper {
  @extend .column-center;
  padding: $base-gap;
  position: relative;
}

.summary-wrapper-light {
  @extend .summary-wrapper;
  background: $light-background;
}

.summary-wrapper-dark {
  @extend .summary-wrapper;
  background: $dark-background;
}

// 
.content-center-wrapper {
  width: 100%;
  max-width: 10rem;
}

/* ---  --- */
// 
.summary-title-light {
  @include font($font-size-title-l, $font-color-light, $font-weight-xx);
  line-height: $line-height-x;
}

.summary-title-dark {
  @extend .summary-title-light;
  color: $font-color-dark;
}


// 
.title-medium-light {
  @include font($font-size-title, $font-color-light, $font-weight-x);
}

.title-medium-dark {
  @extend .title-medium-light;
  color: $font-color-dark;
}

// 
.title-subhead-light {
  @include font($font-size-title-m, $font-color-light, $font-weight-x);
}

.title-subhead-dark {
  @extend .title-subhead-light;
  color: $font-color-dark;
}

// 
.title-small-light {
  @include font($font-size-title-s, $font-color-light, $font-weight-x);
}

.title-small-light-m {
  @extend .title-small-light;
  color: $font-color-light-m;
}

.title-small-dark {
  @extend .title-small-light;
  color: $font-color-dark;
}

.title-small-dark-m {
  @extend .title-small-light;
  color: $font-color-dark-m;
}

// 
.text-normal-light {
  @include font($font-size-normal, $font-color-light);
}

.text-normal-dark {
  @extend .text-normal-light;
  color: $font-color-dark;
}

.text-normal-dark-m {
  @extend .text-normal-light;
  color: $font-color-dark-m;
}

.text-normal-grey {
  @extend .text-normal-light;
  color: $font-color-grey;
}

// 
.text-medium-light {
  line-height: 1.5;
  @include font($font-size-medium, $font-color-light);
}

.text-medium-light-m {
  @include font($font-size-medium, $font-color-light-m);
}
.text-medium-light-s {
  @include font($font-size-medium, $font-color-light-s);
}

.text-medium-dark {
  @extend .text-medium-light;
  color: $font-color-dark;
}

.text-medium-dark-m {
  @extend .text-medium-light;
  color: $font-color-dark-m;
}

.text-medium-dark-s {
  @extend .text-medium-light;
  color: $font-color-dark-s;
}

.text-medium-grey {
  @extend .text-medium-light;
  color: $font-color-grey;
}

// /
.text-small-light {
  @include font($font-size-small, $font-color-light);
}

.text-small-dark {
  @extend .text-small-light;
  color: $font-color-dark;
}

.text-small-light-m {
  @extend .text-small-light;
  line-height: $line-height;
  color: $font-color-light-m;
}

.text-small-light-s {
  @extend .text-small-light;
  //line-height: $line-height;
  color: $font-color-light-s;
}

// 
.text-tiny-light {
  @include font($font-size-tiny, $font-color-light);
}

.text-tiny-light-s {
  @include font($font-size-tiny, $font-color-light-s);
}

.text-tiny-dark-m {
  @extend .text-tiny-light;
  color: $font-color-dark-m;
}

.text-tiny-dark {
  @extend .text-tiny-light;
  color: $font-color-dark;
}

.bold {
  font-weight: $font-weight-x;
}

.omit {
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap
}


/* ---  --- */
.anchor {
  position: absolute;
  top: -$header-height;
  left: 0;
}

/* ---  --- */

.interaction-opacity {
  &:hover { opacity: 0.6; cursor: pointer };
  &:active { opacity: 1; };
}

.custom-message-success {
  top: 70px;
  //width: 155px;
  min-width: 155px;
  height: 40px;
  @include zIndex($alert-z-index, $z-index-top);
  border: 0;
  background: $color-success-opacity;
  & .el-message__img {
    display: none;
  }
  & .el-message__group {
    margin-left: 30px;
  }
  &:after {
    content: '';
    background-image:url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 15px 15px;
    width: 15px;
    height: 15px;
    position: absolute;
    top: 12px;
    left: 20px;
  }
}

.custom-message-danger {
  @extend .custom-message-success;
  background: $color-danger-opacity;
  &:after {
    content: '';
    background-image:url(data:image/png;base64,);
  }
}
