body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

a:hover, a:link, a:hover, a:visited {
  text-decoration: none; }

.align-center {
  text-align: center; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.line-dark {
  height: 1px;
  background: #000; }

.line-dark-m {
  height: 1px;
  background: rgba(0, 0, 0, 0.1); }

.line-light {
  height: 1px;
  background: #fff; }

.line-light-m {
  height: 1px;
  background: rgba(255, 255, 255, 0.1); }

.row, .row-center {
  display: flex;
  align-items: center; }

.column, .column-center, .summary-wrapper, .summary-wrapper-light, .summary-wrapper-dark {
  display: flex;
  flex-direction: column;
  align-items: center; }

.center, .row-center, .column-center, .summary-wrapper, .summary-wrapper-light, .summary-wrapper-dark {
  justify-content: center;
  align-items: center; }

/* --- Summary --- */
.summary-wrapper, .summary-wrapper-light, .summary-wrapper-dark {
  padding: 0.65rem;
  position: relative; }

.summary-wrapper-light {
  background: #ffffff; }

.summary-wrapper-dark {
  background: #120F1B; }

.content-center-wrapper {
  width: 100%;
  max-width: 10rem; }

/* ---  --- */
.summary-title-light, .summary-title-dark {
  font-size: 0.8533rem;
  color: #fff;
  font-weight: 700;
  line-height: 1.12rem; }

.summary-title-dark {
  color: #18181C; }

.title-medium-light, .title-medium-dark {
  font-size: 0.64rem;
  color: #fff;
  font-weight: 500; }

.title-medium-dark {
  color: #18181C; }

.title-subhead-light, .title-subhead-dark {
  font-size: 0.5333rem;
  color: #fff;
  font-weight: 500; }

.title-subhead-dark {
  color: #18181C; }

.title-small-light, .title-small-light-m, .title-small-dark, .title-small-dark-m {
  font-size: 0.48rem;
  color: #fff;
  font-weight: 500; }

.title-small-light-m {
  color: rgba(255, 255, 255, 0.65); }

.title-small-dark {
  color: #18181C; }

.title-small-dark-m {
  color: rgba(24, 24, 28, 0.85); }

.text-normal-light, .text-normal-dark, .text-normal-dark-m, .text-normal-grey {
  font-size: 0.4267rem;
  color: #fff; }

.text-normal-dark {
  color: #18181C; }

.text-normal-dark-m {
  color: rgba(24, 24, 28, 0.85); }

.text-normal-grey {
  color: #7A7A91; }

.text-medium-light, .text-medium-dark, .text-medium-dark-m, .text-medium-dark-s, .text-medium-grey {
  line-height: 1.5;
  font-size: 0.3733rem;
  color: #fff; }

.text-medium-light-m {
  font-size: 0.3733rem;
  color: rgba(255, 255, 255, 0.65); }

.text-medium-light-s {
  font-size: 0.3733rem;
  color: rgba(255, 255, 255, 0.45); }

.text-medium-dark {
  color: #18181C; }

.text-medium-dark-m {
  color: rgba(24, 24, 28, 0.85); }

.text-medium-dark-s {
  color: rgba(0, 0, 0, 0.45); }

.text-medium-grey {
  color: #7A7A91; }

.text-small-light, .text-small-dark, .text-small-light-m, .text-small-light-s {
  font-size: 0.32rem;
  color: #fff; }

.text-small-dark {
  color: #18181C; }

.text-small-light-m {
  line-height: 0.56rem;
  color: rgba(255, 255, 255, 0.65); }

.text-small-light-s {
  color: rgba(255, 255, 255, 0.45); }

.text-tiny-light, .text-tiny-dark-m, .text-tiny-dark {
  font-size: 0.2667rem;
  color: #fff; }

.text-tiny-light-s {
  font-size: 0.2667rem;
  color: rgba(255, 255, 255, 0.45); }

.text-tiny-dark-m {
  color: rgba(24, 24, 28, 0.85); }

.text-tiny-dark {
  color: #18181C; }

.bold {
  font-weight: 500; }

.omit {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

/* ---  --- */
.anchor {
  position: absolute;
  top: -1.28rem;
  left: 0; }

/* ---  --- */
.interaction-opacity:hover {
  opacity: 0.6;
  cursor: pointer; }

.interaction-opacity:active {
  opacity: 1; }

.custom-message-success, .custom-message-danger {
  top: 70px;
  min-width: 155px;
  height: 40px;
  z-index: 20009;
  border: 0;
  background: #f0f9eb; }
  .custom-message-success .el-message__img, .custom-message-danger .el-message__img {
    display: none; }
  .custom-message-success .el-message__group, .custom-message-danger .el-message__group {
    margin-left: 30px; }
  .custom-message-success:after, .custom-message-danger:after {
    content: '';
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 15px 15px;
    width: 15px;
    height: 15px;
    position: absolute;
    top: 12px;
    left: 20px; }

.custom-message-danger {
  background: #fef0f0; }
  .custom-message-danger:after {
    content: '';
    background-image: url(data:image/png;base64,); }

.modal-wrapper {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10009;
  text-align: center;
  transition: top, opacity, 0.3s ease-in-out;
  visibility: hidden;
  background: rgba(0, 0, 0, 0.3);
  opacity: 0; }

.modal-wrapper-active {
  visibility: visible;
  opacity: 1;
  top: 0; }

.modal-content-wrapper, .modal-content-wrapper-active {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  transition: top 0.3s ease-in-out; }

.modal-content-wrapper-active {
  top: 50%; }
