/* ---  --- */
.anchor {
  position: absolute;
  top: -1.28rem;
  left: 0; }

/* ---  --- */
.interaction-opacity:hover {
  opacity: 0.6;
  cursor: pointer; }

.interaction-opacity:active {
  opacity: 1; }

.custom-message-success, .custom-message-danger {
  top: 70px;
  min-width: 155px;
  height: 40px;
  z-index: 20009;
  border: 0;
  background: #f0f9eb; }
  .custom-message-success .el-message__img, .custom-message-danger .el-message__img {
    display: none; }
  .custom-message-success .el-message__group, .custom-message-danger .el-message__group {
    margin-left: 30px; }
  .custom-message-success:after, .custom-message-danger:after {
    content: '';
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 15px 15px;
    width: 15px;
    height: 15px;
    position: absolute;
    top: 12px;
    left: 20px; }

.custom-message-danger {
  background: #fef0f0; }
  .custom-message-danger:after {
    content: '';
    background-image: url(data:image/png;base64,); }

