$font-size-title-l: 0.8533rem;   // 32px

$font-size-title: 0.64rem; // 24px

$font-size-title-m: 0.5333rem; // 20px

$font-size-title-s: 0.48rem;   // 18px

$font-size-normal: 0.4267rem;  // 16px

$font-size-medium: 0.3733rem;  // 14px

$font-size-small: 0.32rem;     // 12px

$font-size-tiny: 0.2667rem;    // 10px

//line-height
$line-height-x: 1.12rem;

$line-height: 0.56rem;

// 
$font-weight-xx: 700;

$font-weight-x: 500;

$font-weight-s: 200;

// 
$base-gap: 0.65rem; // 24px

$header-height: 1.28rem;

// 
$radius: 0.1067rem;
$radius-l: 0.2rem;

/* ---  z-index ---*/
$modal-z-index: 10000;
$alert-z-index: 20000;

$z-index-top: 9;
$z-index-middle: 6;
$z-index-bottom: 3;

/**
 * mix
 */
@mixin font($size: $font-size-normal, $color: $font-color-dark, $weight: null) {
  font-size: $size;
  color: $color;
  @if null != $weight { font-weight: $weight}
}

@mixin zIndex($basic: $modal-z-index, $index: 0) {
  z-index: $basic + $index;
}

@mixin phone {
  @media (max-width: 1200px) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: 1200px) {
    @content;
  }
}
