body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

a:hover, a:link, a:hover, a:visited {
  text-decoration: none; }

.align-center {
  text-align: center; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.line-dark {
  height: 1px;
  background: #000; }

.line-dark-m {
  height: 1px;
  background: rgba(0, 0, 0, 0.1); }

.line-light {
  height: 1px;
  background: #fff; }

.line-light-m {
  height: 1px;
  background: rgba(255, 255, 255, 0.1); }

.row, .row-center {
  display: flex;
  align-items: center; }

.column, .column-center, .summary-wrapper, .summary-wrapper-light, .summary-wrapper-dark {
  display: flex;
  flex-direction: column;
  align-items: center; }

.center, .row-center, .column-center, .summary-wrapper, .summary-wrapper-light, .summary-wrapper-dark {
  justify-content: center;
  align-items: center; }

/* --- Summary --- */
.summary-wrapper, .summary-wrapper-light, .summary-wrapper-dark {
  padding: 0.65rem;
  position: relative; }

.summary-wrapper-light {
  background: #ffffff; }

.summary-wrapper-dark {
  background: #120F1B; }

.content-center-wrapper {
  width: 100%;
  max-width: 10rem; }

/* ---  --- */
.summary-title-light, .summary-title-dark {
  font-size: 0.8533rem;
  color: #fff;
  font-weight: 700;
  line-height: 1.12rem; }

.summary-title-dark {
  color: #18181C; }

.title-medium-light, .title-medium-dark {
  font-size: 0.64rem;
  color: #fff;
  font-weight: 500; }

.title-medium-dark {
  color: #18181C; }

.title-subhead-light, .title-subhead-dark {
  font-size: 0.5333rem;
  color: #fff;
  font-weight: 500; }

.title-subhead-dark {
  color: #18181C; }

.title-small-light, .title-small-light-m, .title-small-dark, .title-small-dark-m, .header-link {
  font-size: 0.48rem;
  color: #fff;
  font-weight: 500; }

.title-small-light-m {
  color: rgba(255, 255, 255, 0.65); }

.title-small-dark {
  color: #18181C; }

.title-small-dark-m {
  color: rgba(24, 24, 28, 0.85); }

.text-normal-light, .text-normal-dark, .text-normal-dark-m, .text-normal-grey, .header-link-row {
  font-size: 0.4267rem;
  color: #fff; }

.text-normal-dark {
  color: #18181C; }

.text-normal-dark-m {
  color: rgba(24, 24, 28, 0.85); }

.text-normal-grey {
  color: #7A7A91; }

.text-medium-light, .text-medium-dark, .text-medium-dark-m, .text-medium-dark-s, .text-medium-grey {
  line-height: 1.5;
  font-size: 0.3733rem;
  color: #fff; }

.text-medium-light-m {
  font-size: 0.3733rem;
  color: rgba(255, 255, 255, 0.65); }

.text-medium-light-s {
  font-size: 0.3733rem;
  color: rgba(255, 255, 255, 0.45); }

.text-medium-dark {
  color: #18181C; }

.text-medium-dark-m {
  color: rgba(24, 24, 28, 0.85); }

.text-medium-dark-s {
  color: rgba(0, 0, 0, 0.45); }

.text-medium-grey {
  color: #7A7A91; }

.text-small-light, .text-small-dark, .text-small-light-m, .text-small-light-s {
  font-size: 0.32rem;
  color: #fff; }

.text-small-dark {
  color: #18181C; }

.text-small-light-m {
  line-height: 0.56rem;
  color: rgba(255, 255, 255, 0.65); }

.text-small-light-s {
  color: rgba(255, 255, 255, 0.45); }

.text-tiny-light, .text-tiny-dark-m, .text-tiny-dark {
  font-size: 0.2667rem;
  color: #fff; }

.text-tiny-light-s {
  font-size: 0.2667rem;
  color: rgba(255, 255, 255, 0.45); }

.text-tiny-dark-m {
  color: rgba(24, 24, 28, 0.85); }

.text-tiny-dark {
  color: #18181C; }

.bold {
  font-weight: 500; }

.omit {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

/* ---  --- */
.anchor {
  position: absolute;
  top: -1.28rem;
  left: 0; }

/* ---  --- */
.interaction-opacity:hover, .header-menu-btn:hover, .login-wrapper:hover {
  opacity: 0.6;
  cursor: pointer; }

.interaction-opacity:active, .header-menu-btn:active, .login-wrapper:active {
  opacity: 1; }

.custom-message-success, .custom-message-danger {
  top: 70px;
  min-width: 155px;
  height: 40px;
  z-index: 20009;
  border: 0;
  background: #f0f9eb; }
  .custom-message-success .el-message__img, .custom-message-danger .el-message__img {
    display: none; }
  .custom-message-success .el-message__group, .custom-message-danger .el-message__group {
    margin-left: 30px; }
  .custom-message-success:after, .custom-message-danger:after {
    content: '';
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 15px 15px;
    width: 15px;
    height: 15px;
    position: absolute;
    top: 12px;
    left: 20px; }

.custom-message-danger {
  background: #fef0f0; }
  .custom-message-danger:after {
    content: '';
    background-image: url(data:image/png;base64,); }

.header-wrapper {
  position: absolute;
  width: 100%;
  height: 1.28rem;
  background: #120F1B;
  padding-left: 0.65rem;
  padding-right: 0.65rem;
  z-index: 10009; }

.header-logo-box, .header-menus-box {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: flex-start; }

.header-menus-box {
  justify-content: space-between;
  overflow: hidden; }
  @media (min-width: 1200px) {
    .header-menus-box {
      overflow: visible; } }

.header-btn-box, .header-btn-drop-box {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: flex-end;
  position: relative;
  overflow: hidden; }

.header-img-menu {
  width: 0.4rem; }

.header-btn-drop-box {
  overflow: visible; }

.header-menu-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 0.8rem;
  width: 0.8rem;
  border-radius: 0.4rem;
  background: #2A2732; }

.header-logo {
  width: 0.8rem;
  margin-left: -0.18rem;
  margin-right: 0.2667rem; }

.header-modal, .header-modal-inactive, .header-modal-active {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 10006; }

.header-modal-inactive {
  visibility: hidden;
  opacity: 0;
  will-change: opacity;
  transition: opacity 0.3s;
  overflow: hidden; }

.header-modal-active {
  visibility: visible;
  opacity: 1; }

.header-menu-content, .header-menu-content-active {
  padding: 1.28rem 0.65rem 1.95rem 0.65rem;
  background: #120F1B;
  margin-top: -1rem;
  transition: margin-top 0.3s; }

.header-menu-content-active {
  margin-top: 0; }

.header-menu-icon {
  color: #fff;
  font-size: 0.3rem; }

.login-wrapper {
  text-align: right;
  background: #2A2732;
  border-radius: 0.1067rem;
  position: absolute;
  top: 0.32rem;
  padding: 0 0.2133rem; }

.header-drop-down-icon {
  color: #fff; }

.login-out-btn {
  height: 0.5333rem;
  line-height: 0.1067rem;
  padding: 0 0.2133rem;
  margin: 0.1067rem 0 0.2133rem 0; }

.header-link {
  cursor: pointer;
  margin: 0.56rem 0;
  height: 0.6667rem; }

.header-link-row {
  cursor: pointer;
  margin: 0; }

.header-menu-drop-down {
  width: 0.28rem;
  position: relative;
  top: 0.05rem;
  margin-left: 0.1rem; }
